"use client"

import { useState } from "react"
import { Button } from "@/components/ui/button"
import { Card } from "@/components/ui/card"
import { Input } from "@/components/ui/input"
import { Slider } from "@/components/ui/slider"
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select"

const GENRES = [
  "Electronic", "Hip Hop", "Rock", "Classical", "Jazz", "Pop", "Ambient", "Folk"
]

const MOODS = [
  "Happy", "Energetic", "Calm", "Melancholic", "Epic", "Romantic", "Dark", "Peaceful"
]

export function MusicGenerator() {
  const [prompt, setPrompt] = useState("")
  const [genre, setGenre] = useState("")
  const [mood, setMood] = useState("")
  const [duration, setDuration] = useState([30])
  const [tempo, setTempo] = useState([120])
  const [isGenerating, setIsGenerating] = useState(false)
  const [generatedAudio, setGeneratedAudio] = useState<string | null>(null)

  const handleGenerate = async () => {
    if (!prompt) return
    
    setIsGenerating(true)
    try {
      // Simulated generation delay
      await new Promise(resolve => setTimeout(resolve, 3000))
      // In a real implementation, this would make an API call to an AI music generation service
      setGeneratedAudio("demo-audio-url.mp3")
    } catch (error) {
      console.error("Generation failed:", error)
    } finally {
      setIsGenerating(false)
    }
  }

  return (
    <Card className="w-full max-w-4xl p-8 bg-zinc-900 border-zinc-800 shadow-xl">
      <div className="space-y-8">
        {/* Main Prompt */}
        <div>
          <label className="block text-lg font-medium mb-3 text-white">
            Describe your music
          </label>
          <Input
            placeholder="E.g., A dreamy electronic track with floating melodies and deep bass"
            value={prompt}
            onChange={(e) => setPrompt(e.target.value)}
            className="bg-zinc-800 border-zinc-700 text-white h-14 text-lg placeholder:text-zinc-500"
          />
        </div>

        {/* Genre and Mood Selection */}
        <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
          <div>
            <label className="block text-lg font-medium mb-3 text-white">
              Genre
            </label>
            <Select value={genre} onValueChange={setGenre}>
              <SelectTrigger className="bg-zinc-800 border-zinc-700 text-white h-14">
                <SelectValue placeholder="Select genre" />
              </SelectTrigger>
              <SelectContent className="bg-zinc-800 border-zinc-700">
                {GENRES.map((g) => (
                  <SelectItem 
                    key={g} 
                    value={g.toLowerCase()}
                    className="text-white hover:bg-zinc-700 focus:bg-zinc-700"
                  >
                    {g}
                  </SelectItem>
                ))}
              </SelectContent>
            </Select>
          </div>

          <div>
            <label className="block text-lg font-medium mb-3 text-white">
              Mood
            </label>
            <Select value={mood} onValueChange={setMood}>
              <SelectTrigger className="bg-zinc-800 border-zinc-700 text-white h-14">
                <SelectValue placeholder="Select mood" />
              </SelectTrigger>
              <SelectContent className="bg-zinc-800 border-zinc-700">
                {MOODS.map((m) => (
                  <SelectItem 
                    key={m} 
                    value={m.toLowerCase()}
                    className="text-white hover:bg-zinc-700 focus:bg-zinc-700"
                  >
                    {m}
                  </SelectItem>
                ))}
              </SelectContent>
            </Select>
          </div>
        </div>

        {/* Duration and Tempo Controls */}
        <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
          <div className="p-4 bg-zinc-800 rounded-lg">
            <label className="block text-lg font-medium mb-3 text-white">
              Duration: {duration}s
            </label>
            <Slider
              value={duration}
              onValueChange={setDuration}
              max={180}
              min={15}
              step={15}
              className="py-4"
            />
          </div>

          <div className="p-4 bg-zinc-800 rounded-lg">
            <label className="block text-lg font-medium mb-3 text-white">
              Tempo: {tempo} BPM
            </label>
            <Slider
              value={tempo}
              onValueChange={setTempo}
              max={200}
              min={60}
              step={5}
              className="py-4"
            />
          </div>
        </div>

        {/* Generate Button */}
        <Button
          onClick={handleGenerate}
          disabled={!prompt || isGenerating}
          className={`w-full h-16 text-xl font-semibold transition-all duration-300 ${
            isGenerating 
              ? 'bg-zinc-700 text-zinc-300 cursor-not-allowed'
              : 'bg-white text-black hover:bg-gray-200 hover:scale-[0.99] active:scale-[0.97]'
          }`}
        >
          {isGenerating ? (
            <span className="flex items-center justify-center gap-3">
              Generating...
              <div className="animate-spin rounded-full h-6 w-6 border-b-2 border-black"></div>
            </span>
          ) : (
            "Generate Music"
          )}
        </Button>

        {/* Generated Audio Preview */}
        {generatedAudio && (
          <div className="mt-8 p-6 bg-zinc-800 rounded-lg space-y-4">
            <h3 className="text-xl font-medium text-white mb-4">Your Generated Track</h3>
            <audio controls className="w-full">
              <source src={generatedAudio} type="audio/mpeg" />
              Your browser does not support the audio element.
            </audio>
            <Button
              variant="outline"
              className="w-full h-12 text-lg border-zinc-600 text-white hover:bg-zinc-700"
              onClick={() => window.open(generatedAudio, "_blank")}
            >
              Download Track
            </Button>
          </div>
        )}
      </div>
    </Card>
  )
}
